import React from 'react';
import styled from 'styled-components';

const Radio = styled.div`
  position: absolute;
  top: 30px;
  right: 30px;
  input {
    display: none;
  }
  label {
    display: inline-block;
    border-radius: 2px;
    width: 30px;
    text-align: center;
    color: #ccc;
    font-weight: 500;
    background-color: rgba(200, 200, 200, 0.5);
  }
  input:checked+label {
    background-color: #00aa00;
  }
`;

const  LanguageSwitch = ({ handleSwitch, language }) => {
  return (
    <Radio>
      <input type="radio" id="ru" name="language" value="ru" onChange={handleSwitch} checked={language} />
      <label htmlFor="ru">
        ru
      </label>
      <input type="radio" id="en" name="language" value="en" onChange={handleSwitch} checked={!language} />
      <label htmlFor="en">
        en
      </label>
      
    </Radio>
  )
}

export { LanguageSwitch } ;