import React from 'react';
import styled from 'styled-components';

import { SSpanRu, SSpanEn } from '../SSpan';

const StyledContacts = styled.div`
  padding-top: 60px;
  padding-left: 20px;
`;

export function DataOfContacts({language}) {
  return (
    <StyledContacts>
      <h1>
        <SSpanRu ru={language}>Я на связи</SSpanRu>
        <SSpanEn en={language}>contact me</SSpanEn>
      </h1>
      <h3>
        <SSpanRu ru={language}>можете связаться со мной по телефону</SSpanRu>
        <SSpanEn en={language}>can contact me by phone</SSpanEn>
      </h3>
      <p><a href="tel:+7 992 343 75 03">+7 992 343 75 03</a></p>
      <h3>
        <SSpanRu ru={language}>или написать на почту</SSpanRu>
        <SSpanEn en={language}>or write to the mail</SSpanEn>
      </h3>
      <p><a href="mailto: buugaaga.ildar@gmail.com">ildar.nasrov@gmail.com</a></p>
      <h3>
        <SSpanRu ru={language}>также в меню есть ссылки на </SSpanRu>
        <SSpanEn en={language}>the menu also have links to </SSpanEn>
        <a href="https://twitter.com/ildarDanilovich" target="_blank" rel="noopener noreferrer">twitter </a>, 
        <a href="https://www.linkedin.com/in/eldar-nasyrov-a0391b177/" target="_blank" rel="noopener noreferrer">linkedin </a>, 
        <a href="https://www.freecodecamp.org/fcc4c90eabd-ea8b-46a1-9916-df4f3255c07c" target="_blank" rel="noopener noreferrer">freecodecamp </a>, 
        <a href="https://github.com/buugaaga" target="_blank" rel="noopener noreferrer">github</a>
      </h3>
    </StyledContacts>
  )
}
